import { navigate } from 'gatsby-link';
import React from 'react';

import YMLBanes from "../../core-rules/banes/banes.yml";
import {default as parseStrToHtml} from 'html-react-parser';

import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import { makeStyles } from '@material-ui/core/styles';

import ArrowBackIcon from '@material-ui/icons/ArrowBack';

const useStyles = makeStyles(({
  baneSection: {
    fontSize: '16px',
    margin: '24px'
  },
  toolbar: {
    backgroundColor: 'rgba(81,45,168, 0.9)',
    color: 'rgba(255,255,255,0.87)',
    fontSize: '20px',
  },
  backButton: {
    '&:hover': {
      backgroundColor: 'transparent'
    }
  },
  backIcon: {
    color: 'rgba(255,255,255,0.87)',
  }
}))

const banesNames = YMLBanes.map(bane => bane.name.toLowerCase().replace(/\s+/, '-'))

export default function SingleItemPageBane(props) {
  const classes = useStyles();

  if (banesNames.indexOf(props.bane) === -1) {
    navigate('/banes')
  }
    return (
      <div className={classes.container}>
        <Toolbar className={classes.toolbar}> 
          <IconButton disableRipple href='/banes' className={classes.backButton} >
            <ArrowBackIcon  className={classes.backIcon} />
          </IconButton>
            Back To Bane List
        </Toolbar>
        <div className={classes.baneSection}>
          {
          YMLBanes.map((bane, index) => {
            if(bane.name.toLowerCase().replace(/\s+/, '-') === props.bane) {
              return (
                <div key={index}>
                  <h2 id={bane.name} style={{ lineHeight: '40px' }}>
                    {bane.name}
                  </h2>
                  <p><strong>Duration: </strong>{bane.duration}</p>
                  <p><strong>Power Level: </strong>{bane.power.map((item, index) => {
                      if (index < bane.power.length - 1) return `${item}/ `;
                      return item;
                    })}
                  </p>
                  <p><strong>Attack Attributes: </strong>{bane.attackAttributes.map((item, index) => {
                      if (index < bane.attackAttributes.length - 1) return `${item}, `;
                      return item;
                    })}
                  </p>
                  <p><strong>Attack: </strong></p>
                  <p></p>
                  <ul>
                    {bane.attack.map((item, index) => {
                      return (
                        <li key={index}>{item}</li>
                      )
                    })}
                  </ul>
                  <p><strong>Description</strong></p>
                  <p>{bane.description}</p>
                  <p><strong>Effect</strong></p>
                  <div>{parseStrToHtml(bane.effect)}</div>
                  <p></p> 
                </div>
                )
            } else {
                return null;
            }
          })}
        </div>
      </div>
    )
}
